import { createApp } from 'vue'
import { createStore } from 'vuex'
//import axios from 'axios'



const store = createStore ({
    state: {
        products: [
            {
                "image": "1.jpg",
                "name": "T-shirt 1",
                "price": 1500,
                "article": "T1",
                "available": true,
                "category": "Male",
                "quantity": 1
            },
            {
                "image": "2.jpg",
                "name": "T-shirt 2",
                "price": 1200,
                "article": "T2",
                "available": true,
                "category": "Female",
                "quantity": 1
            },
            {
                "image": "3.jpg",
                "name": "T-shirt 3",
                "price": 980,
                "article": "T3",
                "available": false,
                "category": "Female",
                "quantity": 1
            },
            {
                "image": "4.jpg",
                "name": "T-shirt 4",
                "price": 1680,
                "article": "T4",
                "available": true,
                "category": "Male",
                "quantity": 1
            },
            {
                "image": "5.jpg",
                "name": "T-shirt 5",
                "price": 1900,
                "article": "T5",
                "available": false,
                "category": "Female",
                "quantity": 1
            },
            {
                "image": "6.jpg",
                "name": "T-shirt 6",
                "price": 1799,
                "article": "T6",
                "available": true,
                "category": "Female",
                "quantity": 1
            },
            {
                "image": "7.jpg",
                "name": "T-shirt 7",
                "price": 1399,
                "article": "T7",
                "available": true,
                "category": "Male",
                "quantity": 1
            },
            {
                "image": "8.jpg",
                "name": "T-shirt 8",
                "price": 1449,
                "article": "T8",
                "available": true,
                "category": "Female",
                "quantity": 1
            },
            {
                "image": "9.jpg",
                "name": "T-shirt 9",
                "price": 999,
                "article": "T9",
                "available": true,
                "category": "Female",
                "quantity": 1
            }
        ],
        cart: []
    },
    mutations: {
        SET_PRODUCTS_TO_STATE: (state, products) => {
            state.products = products;
        },
        SET_CART: (state, product) => {
            if(state.cart.length){
                let isProductExists = false;
                state.cart.map(function(item) {
                    if (item.article === product.article) {
                        isProductExists = true;
                        item.quantity++
                    }
                })
                if(!isProductExists) {
                    state.cart.push(product)
                }
            } else {
                state.cart.push(product)
            }
        },
        REMOVE_FROM_CART: (state, index) => {
            state.cart.splice(index, 1)
        },
        INCREMENT: (state, index) => {
            state.cart[index].quantity++
        },
        DECREMENT: (state, index) => {
            if (state.cart[index].quantity > 1) {
                state.cart[index].quantity--
            }
        }
    },
    actions: {
        /*GET_PRODUCTS_FROM_API({commit}) {
            return axios('http://localhost:3000/products',{
                method: "GET"
            })
            .then((products)=> {
                commit('SET_PRODUCTS_TO_STATE', products.data);                
                return products;
            })
            .catch((error) => {
                console.log(error);
                return error;
            })
        },*/
        ADD_TO_CART({commit}, product) {
            commit('SET_CART', product)
        },
        INCREMENT_CART_ITEM({commit}, index) {
            commit('INCREMENT', index)
        },
        DECREMENT_CART_ITEM({commit}, index) {
            commit('DECREMENT', index)
        },
        DELETE_FROM_CART({commit}, index) {
            commit('REMOVE_FROM_CART', index)
        }
    },
    getters: {
        PRODUCTS(state) {
            return state.products;
        },
        CART(state) {
            return state.cart;
        }
    }
});
export default store;

const app = createApp({})
app.use(store)